import axios from "@/api";

// import { localStorage2json, json2localStorage } from "@/utils/data/store";
// import { INICIALIZAR_FUNCIONES, LOGOUT_FUNCTION } from '@/store/constants';


const state = {
  grupos: [],
  grupos_permisos: [],
  rolMiembro: {},

  is_cargando: false
};

const mutations = {
  MUT_EstablecerRolMiembro(state, rol) {
    // json2localStorage("rolMiembro", rol)
    state.rolMiembro = rol
  },
  clearRolMiembro(state){
    // json2localStorage("rolMiembro", {})
    state.rolMiembro = {}
  },
  setGrupos(state, payload){ state.grupos = payload },
  addGrupo(state, payload){ state.grupos.push(payload) },
  setCargando(state, payload){ state.is_cargando = payload }
};

const actions = {
  // [LOGOUT_FUNCTION]({ commit }) {
  //   commit('clearRolMiembro')
  // },
  // [INICIALIZAR_FUNCIONES]({ commit }) {
  //   commit('MUT_EstablecerRolMiembro')
  // },
  async obtenerGrupos({commit}) {
    try {
      commit("setCargando", true)
      const { data, status } = await axios.instance.get("grupos/grupo/");
      const { items, message } = data;
      const grupos = []
      for(const grupo of items){
        grupos.push({id: grupo.id, nombre: grupo.name, permisos: grupo.permissions.length})
      }
      commit("setGrupos", grupos)
      commit("setCargando", false)
      return { mensaje: message, estado: status }
    } catch (error) {
      commit("setCargando", false)
      return { error: error }
    }
  },
  async obtenerPermisos() {
    try {
      const { data } = await axios.instance.get("roles/permiso/");
      const { items } = data;
      return { items };
    } catch (error) {
      const strError = String(error.response.data.error);
      return { error: strError };
    }
  },
  async AXN_ActualizarRolMiembro(context, {usuario, rol}) {
    try {
      const { data } = await axios.instance.put(`roles/usuario/${usuario}/`, {'user': usuario, 'rol': rol});
      const { message } = data;
      return { message };
    } catch (error) {
      const strError = String(error.response.data.error);
      return { error: strError };
    }
  },
  async AXN_AsignarRolMiembro(context, {usuario, rol}) {
    try {
      const { data } = await axios.instance.post('roles/usuario/', {'user': usuario, 'rol': rol});
      const { message } = data;
      return { message };
    } catch (error) {
      const strError = String(error.response.data.error);
      return { error: strError };
    }
  },
  async AXN_ObtenerRolMiembro({ commit }, {id} ){
    try {
      const { data } = await axios.instance.get(
        `/roles/usuario/${id}/`,
      );
      const { items } = data
      commit("MUT_EstablecerRolMiembro", items.rol)
      return { message: "Ok" }
    } catch (error) {
      // const strMessage = String(error.response.data.message);
      return { message: "No tiene un rol.", error }
    }
  },
};

const getters = {
  GET_idRolMiembro: (state) => state.rolMiembro.id,
  getCargando: (state) => state.is_cargando,
  getGrupos:(state)=> state.grupos
};


export default {
  namespaced: true,
  state,
  mutations,
  actions,
  getters,
};
