<template>
  <v-form ref="formulario" v-model="validacion" lazy-validation>
    <v-file-input
      v-model="archivo"
      @change="cargar"
      accept=".xlsx"
      label="Elige tu archivo"
      outlined
      dense
      :append-outer-icon="archivo && !is_cargando ? 'mdi-cloud-upload' : 'mdi-send-off'"
      @click:clear="clearInmueblesVerificados()"
      @click:append-outer="crearMultipleInmuebles()"
      :rules="[v=> !!v || 'Es necesario escoger un archivo de tipo .xlsx para verificar los inmuebles']"
    />
    <confirmar-aviso :dialog="is_dialogo_aviso" :tipo_mensaje="tipo_mensaje" :obj_mensaje="mensaje" :color="color" @emitConfirmarAviso="setDialogoAviso" />
  </v-form>
</template>

<script>
import ConfirmarAviso from "../../alertas/ConfirmarAviso.vue"
import readXlsxFile from "read-excel-file"
export default{
  data: () => ({
    archivo: null,
    validacion: false,
    is_dialogo_aviso: false,
    tipo_mensaje: "",
    mensaje: {mensaje: "", informacion: {}},
    color: "warning",
  }),
  components: {
    "confirmar-aviso": ConfirmarAviso
  },
  computed:{
    cliente() { return this.$store.getters["cliente/getCliente"] },
    inmuebles(){ return this.$store.getters["inmueble/getInmueblesVerificados"] },
    verificarIn() { return this.$store.getters["inmueble/getVerificarInmuebles"] },
    is_cargando() { return this.$store.getters["inmueble/getCargando"] },
    numeroInmueblesCreados(){ return this.$store.getters["inmueble/getNumeroInmueblesCreados"] },
    inmuebles_verificados() { return this.$store.getters["inmueble/getTotalInmueblesVerificados"] }
  },
  methods:{
    setDialogoAviso({confirmacion}){
      this.is_dialogo_aviso = confirmacion
    },
    async cargar(){
      var inmuebles = []
      if (this.$refs.formulario.validate()) {
        const {is_valido, cabecera} = await readXlsxFile(this.archivo).then((rows) => {
          const indice_cabecera = this.getIndiceCabecera(rows) /** Va a buscar el indice de la fila(cabecera) de los inmuebles */
          if(indice_cabecera.fila == -1){ /** si indice_cabecera retorna -1, quiere decir que no encontro la cabecera */
            console.log("Index_not_found")
            return
          }
          const {is_valido, cabecera} = this.setIndexCabecera(rows[indice_cabecera.fila])
          if(is_valido){
            for (var fila = indice_cabecera.fila+1; fila < rows.length; ++fila){
              inmuebles.push({
                'cliente': this.cliente,
                'nombre': rows[fila][cabecera.nombre] != null ? rows[fila][cabecera.nombre] : "",
                'clave': rows[fila][cabecera.clave] != null ? rows[fila][cabecera.clave] : "",
                'clave_2': rows[fila][cabecera.clave_2] != null ? rows[fila][cabecera.clave_2] : "",
                'direccion': rows[fila][cabecera.direccion] != null ? rows[fila][cabecera.direccion] : "Dirección no asignada.",
                'estado': rows[fila][cabecera.estado] != null ? rows[fila][cabecera.estado] : "",
                'municipio': rows[fila][cabecera.municipio] != null ? rows[fila][cabecera.municipio] : "",
                'localidad': rows[fila][cabecera.localidad] != null ? rows[fila][cabecera.localidad] : "",          
              })
            }        
          }
          return {is_valido: is_valido, cabecera: cabecera}
        })
        if(is_valido){
          this.$store.commit("inmueble/setVerificarInmuebles", inmuebles)
          await this.verificarInmuebles()
        } else{
          this.tipo_mensaje="Faltan datos en la cabecera"
          this.mensaje.mensaje="No se encontraron los datos en la cabecera del Excel, para poder verificar los inmuebles."
          this.mensaje.informacion=cabecera
          this.is_dialogo_aviso = true
          this.clearInmueblesVerificados()
          this.archivo=null
        }
      }
    },
    async verificarInmuebles(){ 
      const {error, estado} = await this.$store.dispatch("inmueble/verificarInmuebles", this.verificarIn)
      this.$store.commit("alerta/addAlertas", { "estado": estado, "mensaje": `Se verificaron ${this.inmuebles.length} inmuebles.`, "error": error })
    },
    async crearMultipleInmuebles(){ 
      const {error, estado} = await this.$store.dispatch("inmueble/crearMultiplesInmuebles", this.inmuebles)
      this.$store.commit("alerta/addAlertas",{ 
        "estado": estado, "mensaje": `Se crearon ${this.numeroInmueblesCreados} de ${this.inmuebles_verificados} inmuebles.`, "error": error
      })
    },
    clearInmueblesVerificados(){
      this.$store.commit("inmueble/clearInmueblesVerificados")
      this.$store.commit("inmueble/clearVerificarInmuebles")
    },
    getIndiceCabecera(rows){
      for (var fila = 0; fila < rows.length; ++fila){
        var contador_null = 0
        for (var columna = 0; columna < rows[fila].length; ++columna){ /** [a, b, null, c, null, null, ... ] */
          if(contador_null == 5) {break} /** si el contador null encuentra 5 null después de leer el último dato de la fila y columna, dejará de buscar */
          else if(rows[fila][columna] == null){
            contador_null = contador_null + 1
          }
          else contador_null = 0
        }
        if(contador_null < 5){
          return { fila: fila } /** Si entro a esta condición, quiere decir que en la fila encontró la cabecera de los inmuebles. */
        }
      }
      return { fila: -1 }
    },
    normalizarPalabra(element) {
      const acentos = { á: "a", é: "e", í: "i", ó: "o", ú: "u", Á: "A", É: "E", Í: "I", Ó: "O", Ú: "U",};
      if(element === null || Number.isInteger(element)){
        return ""
      } else{ 
        return element.split("").map((letra) => acentos[letra] || letra).join("").toString().toLowerCase(); // quita acentos y pasalo a minisculas
      }
    },
    setIndexCabecera(fila){
      var cabecera= { /** null: obligatorio, -1: opcional */
        nombre: null,
        clave: null,
        direccion: null,
        estado: null,
        municipio: null,
        localidad: -1,
        clave_2: -1
      }
      fila.forEach((element, indiece) => {
        let nombre_cabecera = this.normalizarPalabra(element)
        if(nombre_cabecera.length){
          if (nombre_cabecera === 'nombre' || nombre_cabecera === 'nombre del inmueble') {
            cabecera.nombre = indiece
          } else if(nombre_cabecera === 'clave' || nombre_cabecera === 'cr' || nombre_cabecera === 'codigo' || nombre_cabecera === 'id' || nombre_cabecera === "id inmueble"){
            cabecera.clave = indiece
          } else if(nombre_cabecera === 'direccion' || nombre_cabecera === 'calle y numero'){
            cabecera.direccion = indiece
          } else if(nombre_cabecera === 'edo.' || nombre_cabecera === 'estado'){
            cabecera.estado = indiece
          } else if(nombre_cabecera === 'municipio' || nombre_cabecera === 'municipio'){
            cabecera.municipio = indiece
          } else if(nombre_cabecera === 'ciudad' || nombre_cabecera === 'localidad'){
            cabecera.localidad = indiece
          } else if(nombre_cabecera === 'convive' || nombre_cabecera === 'sirh' || nombre_cabecera === 'convivencia'){
            cabecera.clave_2 = indiece
          }
        }
      })
      const array_valores_cabeceras = Object.values(cabecera)
      const is_valido = array_valores_cabeceras.find(element => element === null) === undefined ? true : false
      return { is_valido: is_valido, cabecera: cabecera }
    },
  }
}
</script>