<template>
  <v-dialog v-model="dialog" persistent max-width="300">
    
      <v-card class="mx-auto">
        <v-card-title class="text-h6 grey lighten-2">
          {{tipo_mensaje}}
        </v-card-title>
        <v-card-text class="mt-5">
          {{obj_mensaje.mensaje}}
          <v-list v-if="Object.keys(obj_mensaje.informacion).length" subheader>
            <v-list-item v-for="valor of Object.keys(obj_mensaje.informacion)" :key="valor" dense>
              <v-list-item-content>
                <v-list-item-title>{{valor.toUpperCase()}}</v-list-item-title>
              </v-list-item-content>
              <v-list-item-action>
                <v-icon v-if="obj_mensaje.informacion[valor] === null" color="error">mdi-close</v-icon>
                <v-icon v-else color="success">mdi-check</v-icon>
              </v-list-item-action>
            </v-list-item>
          </v-list>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="green darken-1" text @click="aceptarConfirmacion()">
            Aceptar
          </v-btn>
        </v-card-actions>
      </v-card>
  </v-dialog>
</template>

<script>
export default {
  props: {
    dialog: {type: Boolean, default: false},
    obj_mensaje: { type: Object, default:() => ({mensaje: "", informacion: {}}) },
    tipo_mensaje: { type: String, default: "" },
    color: { type: String, default: 'primary' }
  },
  methods: {
    aceptarConfirmacion(){
      this.$emit("emitConfirmarAviso", { 'confirmacion':false })
    },
  },
};
</script>