<template>
  <v-row class="container">
    <v-col v-for="(archivo, index) in archivos" :key="index" cols="12" sm="2" md="2" lg="2" xl="2">
      <v-hover v-slot="{ hover }">
        <v-card :elevation="hover ? 12 : 2">
          <v-card-text>
            <v-avatar tile size="100" color="grey lighten-2">
              <v-icon x-large>mdi-paperclip</v-icon>
            </v-avatar>
          </v-card-text>
          <span :class="{ 'show-btns': hover }">Nombre archivo</span>
          <v-card-actions>
            <v-btn :class="{ 'show-btns': !hover }" icon @click="d(archivo)">
              <v-icon> mdi-download </v-icon>
            </v-btn>
            <confirmar-eliminar-archivo
              mensaje="Estás a punto de eliminar esta archivo. ¿Estás seguro?"
              :id="archivo.id"
              :indice="index"
              :tipo_pago="('¿Eliminar ' + archivo.nombre +'?')"
              @emitConfirmacion="eliminarArchivoRecurso"
              icon="mdi-delete"
              :color="!hover ? 'rgba(255, 255, 255, 1) !important': ''"
            />
          </v-card-actions>
        </v-card>
      </v-hover>
    </v-col>
  </v-row>
</template>

<script>
import ConfirmarEliminarArchivo from "../../alertas/Confirmar.vue";

export default {
    components: {
    "confirmar-eliminar-archivo": ConfirmarEliminarArchivo
  },
  computed:{
    archivos() { return this.$store.getters["recurso/getArchivos"] }
  },
  methods:{
    async eliminarArchivoRecurso({confirmacion, id, indice }){
      if(confirmacion){
        const { mensaje, estado, error } = await this.$store.dispatch("recurso/eliminarArhivoRecurso", { 'id': id, 'indice': indice })
        this.$store.commit("alerta/addAlertas", { 'estado': estado, 'mensaje': mensaje, 'error': error, });
      }
    },
    d({archivo}){
      window.open(archivo)
    }
  }
};
</script>
<style scoped>
.show-btns {
  color: rgba(255, 255, 255, 1) !important;
}
</style>