<template>
  <v-card outlined>
    <v-card-text>
        <v-responsive class="overflow-y-auto" max-height="500">
        <v-row justify="center">
          <v-col
            cols="12"
            sm="6"
            v-for="(asignacion, indice_asignacion) in asignaciones"
            :key="indice_asignacion"
          >
            <v-card class="mx-auto" outlined>
              <v-card-title class="grey lighten-2">
                Inmueble: {{ asignacion.inmueble.name }} ({{asignacion.inmueble.property_key}})
                <v-spacer></v-spacer>
                <confirmar
                  mensaje="Estás a punto de eliminar la asignación junto con todos sus trabajos."
                  :indice="indice_asignacion"
                  :tipo_pago="'¿Confirmar eliminacion ' + asignacion.inmueble.name + '?'"
                  @emitConfirmacion="confirmarRecurso"
                  icon="mdi-delete"
                  color="error"
                />
              </v-card-title>
              <v-card-subtitle class="grey lighten-2">
                {{ asignacion.trabajos.length }} {{ asignacion.trabajos.length == 1 ? "trabajo" : "trabajos" }} por asignar
              </v-card-subtitle>
              <v-card-text> <!-- Hacer componente -->
                <v-simple-table dense>
                  <template v-slot:default>
                    <thead>
                      <tr>
                        <th>Concepto</th>
                        <th>Asignado</th>
                        <th>Estado</th>
                        <th>Mensaje</th>
                        <th>Confirmar</th>
                        <th>Eliminar</th>
                      </tr>
                    </thead>
                    <tbody>
                      <tr
                        v-for="(trabajo, index) in asignacion.trabajos"
                        :key="index"
                      >
                        <td>{{ trabajo.concepto.name }} ({{ trabajo.concepto.project__name }})</td>
                        <td>
                          <v-tooltip bottom>
                            <template v-slot:activator="{on, attrs}">
                              <v-badge bordered color="error" icon="mdi-close" overlap disabled >
                                <v-btn
                                  v-on="on"
                                  v-bind="attrs"
                                  @click="activarDialogoUsuario({
                                    id_usuario: trabajo.usuario_asignado.id, indice_inmueble: indice_asignacion, indice_trabajo: index
                                  })"
                                  color="primary"
                                  icon
                                  outlined
                                >
                                  {{trabajo.usuario_asignado.name[0].toUpperCase()}}
                                </v-btn>
                              </v-badge>
                            </template>
                            <span>{{ trabajo.usuario_asignado.name }}</span>
                          </v-tooltip>
                        </td>
                        <td>{{trabajo.estado.name}}</td>
                        <td>
                          <span
                            :class="trabajo.mensaje === 'A este inmueble ya se le asignó este trabajo.' ? 'warning' : 'success'"
                            style="display: flex; text-align: center"
                          >
                            {{ trabajo.mensaje}}
                          </span>
                        </td>
                        <td style="text-align: center">
                          <v-btn
                            icon
                            :color="trabajo.confirmacion ? 'success': 'error'"
                            @click="setConfirmacionTrabajo({indice_asignacion: indice_asignacion, indice_trabajo: index, confirmacion: trabajo.confirmacion})"
                          >
                            <v-icon>{{ trabajo.confirmacion ? "mdi-check" : "mdi-close" }}</v-icon>
                          </v-btn>
                        </td>
                        <td style="text-align: center">
                          <confirmar
                            :mensaje="'Estás a punto de eliminar el trabajo ' + trabajo.concepto.name + ' del inmueble ' + asignacion.inmueble.name + '.'"
                            :indice="indice_asignacion"
                            :id="index"
                            :tipo_pago="'¿Confirmar eliminacion ' + trabajo.concepto.name + '?'"
                            @emitConfirmacion="confirmarTrabajo"
                            icon="mdi-close"
                            color="error"
                          />
                        </td>
                      </tr>
                    </tbody>
                  </template>
                </v-simple-table>
              </v-card-text>
            </v-card>
          </v-col>
        </v-row>
      </v-responsive>
      </v-card-text>
      <v-card-actions persistent>
        <v-btn block color="success" @click="asignar">Crea trabajos</v-btn>
      </v-card-actions>
      <cambiar-usuario
        :objetoDialgoCambiarUsario="objetoDialgoCambiarUsario"
        @setDialogo="setDialogoCambiarUsuario"
      />
    </v-card>
</template>
<script>
import confirmacion from "../alertas/Confirmar.vue"
import cambiarUsuario from "./Usuario/DialogoListaUsuarios.vue"
export default {
  data:()=> ({
    objetoDialgoCambiarUsario: {
      is_dialogo: false,
      id_usuario: 0,
      indice_inmueble: -1,
      indice_trabajo: -1,
    },
  }),
  components: {
    "confirmar": confirmacion,
    "cambiar-usuario": cambiarUsuario
  },
  computed: {
    asignaciones() { return this.$store.getters["trabajo/getAsignacionesVerificadas"]; },
    inmueblesSeleccionados() { return this.$store.getters["inmueble/getInmueblesSeleccionados"]; },
  },
  methods: {
    setDialogoCambiarUsuario({is_dialogo, objetoUsuario, indice_inmueble, indice_trabajo}){
      const { objetoDialgoCambiarUsario } = this
      objetoDialgoCambiarUsario.is_dialogo = is_dialogo
      objetoDialgoCambiarUsario.id_usuario = objetoUsuario.id
      objetoDialgoCambiarUsario.indice_inmueble = indice_inmueble
      objetoDialgoCambiarUsario.indice_trabajo = indice_trabajo
      if(objetoUsuario.id){
        this.$store.commit("trabajo/setUsuarioAsignacionVerificada", {
          indice_asignacion: indice_inmueble, indice_trabajo: indice_trabajo, objetoUsuario: objetoUsuario
        })
      }
    },
    confirmarRecurso({confirmacion, indice}){
      if(confirmacion){
        this.$store.commit("trabajo/spliceAsignacionVerificada", indice)
      }
    },
    confirmarTrabajo({confirmacion, indice, id}){
      if(confirmacion && this.asignaciones[indice].trabajos.length > 1){
        this.$store.commit("trabajo/spliceTrabajoAsignacionVerificada", {indice_asignacion: indice, indice_trabajo: id})
      } else {
        this.confirmarRecurso({confirmacion, indice})
      }
    },
    activarDialogoUsuario({id_usuario, indice_inmueble, indice_trabajo}){
      const { objetoDialgoCambiarUsario } = this
      objetoDialgoCambiarUsario.is_dialogo = true
      objetoDialgoCambiarUsario.id_usuario = id_usuario
      objetoDialgoCambiarUsario.indice_inmueble = indice_inmueble
      objetoDialgoCambiarUsario.indice_trabajo = indice_trabajo
    },
    setConfirmacionTrabajo({indice_asignacion, indice_trabajo, confirmacion}){
      this.$store.commit("trabajo/setConfirmacionConfirmacionTrabajo", {
        indice_asignacion: indice_asignacion, indice_trabajo: indice_trabajo, payload: !confirmacion
      })
    },
    async asignar() {
      const { asignaciones } = this;
      const { error, estado, mensaje } = await this.$store.dispatch("trabajo/confirmarAsignacion", asignaciones );
      this.$store.commit("alerta/addAlertas", { estado: estado, mensaje: mensaje, error: error, });
    },
  }
};
</script>
