<template>
  <v-row>
    <v-col cols="12" sm="12">
      <v-data-table
        :headers="cabecera"
        :items="recurso.peticiones"
        loading-text="Cargando... por favor espere"
        :footer-props="{
          itemsPerPageText: 'Filas por página',
          pageText: '',
        }"
        no-data-text="No tienes trabajos."
        class="elevation-5 row-pointer"
        :loading="cargando"
      >
        <template v-slot:item.importe="{ item }">
          $ {{ item.importe.toFixed(2) }}
        </template>
        <template v-slot:item.creado="{}">
          {{
            [
              new Date(recurso.creado).getMonth() + 1,
              new Date(recurso.creado).getDate(),
              new Date(recurso.creado).getFullYear(),
            ].join("/")
          }}
        </template>
      </v-data-table>
    </v-col>
  </v-row>
</template>

<script>
export default {
  props: {
    cabecera: { type: Array, default: () => [] },
    recurso: { type: Object, default: () => ({}) },
  },
  computed: {
    cargando() {
      return this.$store.getters["recurso/getCargando"];
    },
  },
};
</script>