<template>
  <v-card outlined class="mx-auto">
    <v-card-title class="grey lighten-2">Datos generales de la solicitud de recurso</v-card-title>
    <br>
    <v-card-text>
      <v-form v-model="valid" ref="formulario" lazy-validation>
        <v-row dense>
          <v-col cols="12" sm="4">
            <v-combobox v-model="solicitud_recurso.tipo_pago"
              :items="pagos"
              label="Tipo de pago"
              :rules="[(v) => !!v || 'Es necesario elegir el tipo de pago']"
              outlined
              clearable
            />
          </v-col>
          <v-col cols="12" sm="4">
            <v-textarea
            v-model="solicitud_recurso.concepto_pago"
            outlined
            label="Concepto de pago"
            :rules="[(v) => !!v || 'Es necesario escribir los datos bancarios']"
            auto-grow
            rows="1"
            row-height="15"
            />
          </v-col>
          <v-col cols="12" sm="4">
            <v-text-field type="number" label="Importe" :value=total disabled outlined />
          </v-col>
          <v-col cols="12" sm="12">
            <v-responsive class="overflow-y-auto" max-height="500">
              <v-simple-table dense class="container">
                <template v-slot:top>
                  <v-toolbar flat color="primary lighten-2" >
                    <v-toolbar-title>Peticiones</v-toolbar-title>
                  </v-toolbar>
                </template>
                <template v-slot:default>
                  <thead>
                    <tr>
                      <th class="text-left"> Concepto </th>
                      <th class="text-left"> Inmueble </th>
                      <th class="text-left"> Beneficiario </th>
                      <th class="text-left"> Datos bancarios </th>
                      <th class="text-left"> Banco </th>
                      <th class="text-left"> Importe </th>
                    </tr>
                  </thead>
                  <tbody>
                    <tr v-for="(peticion, index) in peticiones_verificadas" :key="peticion.trabajo" :class="peticion.error ? 'error lighten-3' : ''">
                      <td><b>{{ trabajos_seleccionados[index].concepto }}</b> ({{ trabajos_seleccionados[index].proyecto }})</td>
                      <td>{{ trabajos_seleccionados[index].inmueble.nombre }} (<b>{{ trabajos_seleccionados[index].inmueble.clave }}</b>)</td>
                      <td>{{ peticion.beneficiario ? peticion.beneficiario.nombre : "n/a"}}</td>
                      <td><b>{{ peticion.metodo_pago }}:</b> {{ peticion.datos_bancarios }}</td>
                      <td>{{ peticion.banco }}</td>
                      <td>{{ peticion.monto ? '$' + parseFloat(peticion.monto).toFixed(2) : ''}}</td>
                    </tr>
                  </tbody>
                </template>
              </v-simple-table>
            </v-responsive>
          </v-col>
        </v-row>
      </v-form>
    </v-card-text>
  </v-card>
</template>

<script>
export default {
  data:() =>({
    pagos: ["Transferencia", "Cheque"],
    continuar_p4: false,
    solicitud_recurso: {
      tipo_pago: "",
      concepto_pago: "",
      pago_separado: false,
    },
  }),
  computed: {
    peticiones_verificadas() { return this.$store.getters["recurso/getPeticionesVerificadas"] },
    trabajos_seleccionados() { return this.$store.getters["trabajo/getTrabajosSeleccionados"]; },
    valid: {
      get(){ return this.continuar_p4 },
      set(nuevoValor){
        this.$emit("eventoContinuar", { 'isValid_p3': nuevoValor, 'validacion': this.$refs.formulario})
        if (this.$refs.formulario) { this.$store.commit("recurso/setSolicitudRecurso", this.solicitud_recurso) }
      }
    },
    total() {
      var total = 0.0
      for(const peticion of this.peticiones_verificadas){
        total = peticion.monto ? Number(peticion.monto) + total : 0.0 + total
      }
      return total.toFixed(2)
    },
  },
};
</script>