<template>
  <v-card class="mx-auto" outlined max-width="500">
    <v-card-title class="grey lighten-2">Crear nuevo inmueble
      <v-spacer />
        <v-btn icon fab color="error" @click="cerrarDialgo({isInmuebl_creado: false})">
          <v-icon>mdi-close</v-icon>
        </v-btn>
    </v-card-title>
    <v-card-text class="mt-4">
      <v-form ref="formulario" v-model="validacion" lazy-validation>
        <v-text-field
          v-model="inmueble.nombre"
          label="Nombre*"
          :rules="true ? [(v) => !!v || 'Es necesario un nombre'] : []"
          dense
          outlined
          filled
          clearable
        />
        <v-text-field
          v-model="inmueble.clave"
          label="Clave*"
          :rules="[(v) => !!v || 'Es necesario una clave']"
          outlined
          filled
          clearable
        />
        <v-text-field
          v-model="inmueble.clave_2"
          label="Clave (SIRH, Convive)"
          :rules="[(v) => !!v || 'Es necesario una clave']"
          outlined
          filled
          clearable
        />
        <v-autocomplete
          v-model="inmueble.estado"
          label="Estado*"
          :rules="[(v) => !!v || 'Es necesario elejir un Estado']"
          :items="estados"
          item-text="nombre"
          no-data-text="No se encontraron estados."
          @change="inmueble.estado ? obtenerMunicipiosDeEstado(inmueble.estado): $store.commit('ubicacion/clearMunicipios')"
          return-object
          outlined
          filled
          clearable
        />
        <v-autocomplete
          v-model="inmueble.municipio"
          label="Municipio*"
          :rules="[(v) => !!v || 'Es necesario elejir un Municipio']"
          :items="municipios"
          item-text="nombre"
          no-data-text="No se encontraron municipios."
          return-object
          outlined
          filled
          clearable
        />
        <v-autocomplete
          v-model="inmueble.localidad"
          label="Localidad"
          item-text="nombre"
          :items="localidades"
          return-object
          no-data-text="No se encontraron localidades."
          outlined
          filled
          clearable
        />
        <v-textarea
          v-model="inmueble.direccion"
          label="Direccion"
          auto-grow
          rows="1"
          row-height="15"
          outlined
          filled
          clearable
        />
      </v-form>
    </v-card-text>
    <v-card-actions>
      <v-spacer />
      <v-btn @click="crearInmueble()" :disabled="!validacion" color="success" >Crear inmueble</v-btn>
      <v-btn @click="cerrarDialgo({isInmuebl_creado: false})" :disabled="!validacion" color="error">Cerrar</v-btn>
    </v-card-actions>
  </v-card>
</template>

<script>
export default {
  props: {
    dialog: {type: Boolean, default: false}
  },
  data: () => ({
    validacion: false,
  }),
  computed: {
    inmueble() { return this.$store.getters["inmueble/getInmueble"] },
    cliente() { return this.$store.getters["cliente/getCliente"]; },
    estados() { return this.$store.getters["ubicacion/getEstados"]; },
    municipios() { return this.$store.getters["ubicacion/getMunicipios"]; },
    localidades() { return this.$store.getters["ubicacion/getLocalidades"]; },
  },
  methods: {
    async inicializarEstados() {
      await this.$store.dispatch("ubicacion/obtenerEstados");
    },
    async obtenerMunicipiosDeEstado({ id }) {
      await this.$store.dispatch("ubicacion/obtenerMunicipiosDeEstado", id);
    },
    async obtenerLocalidadesDeMunicipio({ id }) {
      await this.$store.dispatch("ubicacion/obtenerLocalidadesDeMunicipio", id);
    },
    async crearInmueble() {
      if (this.$refs.formulario.validate()) {
        this.inmueble.cliente = this.cliente.id
        const {mensaje, estado, error } = await this.$store.dispatch("inmueble/crearInmueble", this.inmueble)
        this.$store.commit("alerta/addAlertas", { "estado": estado, "mensaje": mensaje, "error": error })
        if(!error){
          this.cerrarDialgo({isInmuebl_creado: true})
        }
      }
    },
    cerrarDialgo({isInmuebl_creado}){
      this.$emit("eventSetDialog", {isDialog: false, isInmuebl_creado: isInmuebl_creado})
      this.$refs.formulario.reset()
    },
  },
};
</script>
