<template>
    <v-row no-gutters justify="center">
        <v-col cols="12" sm="8" md="8" lg="8" xl="8" xs="8">
            <v-card>
                <grupos-permisos :grupos="grupos" :cabecera="cabecera"/>
            </v-card>
        </v-col>
    </v-row>
</template>

<script>
import GruposPermisos from "../../components/Usuario/TablaGrupos.vue"
export default {
    components:{
        "grupos-permisos": GruposPermisos
    },
    data:()=>({
        cabecera: [
            { text: "Nombre", value: "nombre" },
            { text: "Num. de permisos", value: "permisos" },
            { text: "Accion" }
        ]
    }),
    computed:{
        grupos(){ return this.$store.getters["grupo/getGrupos"] }
    },
    created(){
        this.obtenerGrupos()
    },
    methods:{
        async obtenerGrupos(){
            const { mensaje, error, estado } = await this.$store.dispatch("grupo/obtenerGrupos")
            this.$store.commit("alerta/addAlertas", { "estado": estado, "mensaje": mensaje, "error": error })
        }
    }
}
</script>