<template>
  <v-dialog v-model="dialog" persistent max-width="800">
    <template v-slot:activator="{ on, attrs }">
      <v-btn class="ma-2" color="success" dark v-bind="attrs" v-on="on">
        <v-icon dark left> mdi-account-plus </v-icon> Agregar miembro 
      </v-btn>
    </template>
    <v-card class="mx-auto" :loading="is_cargando" :disabled="is_cargando">
      <v-card-title class="grey lighten-2">
        Agregar miembro
        <v-spacer></v-spacer>
        <v-btn icon color="error" @click="cerrar()"><v-icon>mdi-close</v-icon></v-btn>
      </v-card-title>
      <v-card-text>
        <v-form class="container" ref="form" lazy-validation>
            <v-row dense>
              <v-col cols="12" md="4">
                <v-text-field
                  v-model="usuario.nombre"
                  :rules="[(v) => !!v || 'Se requiere un nombre']"
                  label="Nombre*"
                  outlined
                  dense
                />
              </v-col>
              <v-col cols="12" md="4">
                <v-text-field
                  v-model="usuario.apellidos"
                  label="Apellidos*"
                  :rules="[(v) => !!v || 'El apellido es obligatorio']"
                  outlined
                  dense
                />
              </v-col>
              <v-col cols="12" md="4">
                <v-text-field
                  v-model="usuario.nombre_usuario"
                  label="Nombre usuario*"
                  :rules="[(v) => !!v || 'Se requiere nombre de usuario']"
                  outlined
                  dense
                />
              </v-col>
              <v-col cols="12" md="6">
                <v-text-field
                  v-model="usuario.correo"
                  label="Correo*"
                  :rules="[(v) => !!v || 'Correo electronico es requerido']"
                  outlined
                  dense
                />
              </v-col>
              <v-col cols="12" md="6">
                <v-select
                  v-model="dominio"
                  :items="dominios"
                  label="Dominios"
                  outlined
                  dense
                />
              </v-col>
              <v-col cols="12" md="12">
                <v-text-field
                  v-model="usuario.contraseña"
                  :append-icon="mostrarContrasena ? 'mdi-eye' : 'mdi-eye-off'"
                  :rules="[(v) => !!v || 'Requerido.']"
                  :type="mostrarContrasena ? 'text' : 'password'"
                  name="input-10-1"
                  label="Contraseña*"
                  @click:append="mostrarContrasena = !mostrarContrasena"
                  outlined
                  dense
                />
              </v-col>
              <v-col cols="12" md="6">
                <v-checkbox
                  v-model="is_personal"
                  label="¿Es un miembro del personal?"
                />
              </v-col>
            </v-row>
          </v-form>
        </v-card-text>
        <v-card-actions>
          <v-spacer />
          <v-btn @click="cerrar()" color="red" class="ma-3" dark>Cerrar</v-btn>
          <v-btn @click="crearMiembro()" color="green" dark>Agregar</v-btn>
        </v-card-actions>
    </v-card>
  </v-dialog>
</template>

<script>
// import newGroup from '../../components/groups/CreateNewGroup.vue'

export default {
  data: () => ({
    dominio: "@advr.mx",
    groups: [],
    grupos_permisos: [],
    dialog: false,
    dominios: ["@advr.mx", "@gesscorpzero.com"],
    mostrarContrasena: false,
    is_personal: true
  }),
  computed: {
    usuario(){ return this.$store.getters["usuario/getUsuario"] },
    is_cargando() { return this.$store.getters["usuario/getCargando"] },
  },
  methods: {
    async crearMiembro() {
      if (this.$refs.form.validate()) {
        const { nombre_usuario, correo, contraseña, nombre, apellidos } = this.usuario;
        const correo_electronico = correo.concat(this.dominio)
        const { mensaje, estado, error } = await this.$store.dispatch("usuario/crearUsuario", {
          nombre_usuario, correo_electronico, contraseña, nombre, apellidos, is_personal: this.is_personal
        });
        this.$store.commit("alerta/addAlertas", { "estado": estado, "mensaje": mensaje, "error": error })
        if(!error){
          this.cerrar()
        }
      }
    },
    cerrar() {
      this.$refs.form.reset();
      this.$refs.form.resetValidation();
      this.dialog = false;
      this.dominio = "@advr.mx"
    },
  },
};
</script>
