<template>
    <v-dialog v-model="is_dialogo" max-width="300">
        <template v-slot:activator="{on, attrs}">
            <span v-on="on" v-bind="attrs">Gestionar privilegios</span>
        </template>
        <v-card>
            <v-card-title class="grey lighten-2">Permisos de {{usuario.nombre}}</v-card-title>
            <v-card-text>
                <v-sheet v-for="grupo of grupos" :key="grupo.id">
                    <v-switch
                        v-model="grupo.is_active"
                        :label="`${grupo.nombre}: ${grupo.is_active ? 'SI' : 'NO'}`"
                        inset
                        @change="setActivar(grupos)"
                        />
                    </v-sheet>
                </v-card-text>
                <v-card-actions>
                    <v-spacer />
                    <v-btn color="error" class="ma-2">Cancelar</v-btn>
                    <v-btn color="success">Aceptar</v-btn>
                </v-card-actions>
            </v-card>
    </v-dialog>
</template>

<script>

export default {
    props:{
        usuario:{ type: Object, default:()=>({}) },
    },
    data:()=> ({
        dialogo: false,
        is_administrador: true,
        is_coordinador: false,
    }),
    computed:{
        is_dialogo: {
            get(){ return this.dialogo },
            set(nuevoValor){
                this.dialogo = nuevoValor
                if (nuevoValor){ this.obtenerGrupos() }
            }
        },
        grupos() {
            const grupos_usuario = []
            const { grupos } = this.usuario
            for(const grupo of this.$store.getters["grupo/getGrupos"]){
                let is_active = grupos.includes(grupo.id) ? true : false
                grupos_usuario.push({'id': grupo.id, 'nombre': grupo.nombre, is_active: is_active})
            }
            return grupos_usuario
        }
    },
    methods:{
        async obtenerGrupos(){
            await this.$store.dispatch("grupo/obtenerGrupos")
        },
        setActivar(evento){
            console.log(evento)
            console.log(this.grupos)
        }
    }
}
</script>