<template>
  <v-dialog v-model="dialogo" transition="dialog-top-transition" max-width="500" persistent>
    <template v-slot:default="dialog">
      <v-card class="mx-auto" elevation="5" :loading="cargando">
        <v-card-title class="grey lighten-2">
          Crear nuevo estado para el trabajo
          <v-spacer />
          <v-btn @click="cerrrarDialogo(dialog)" color="error" icon><v-icon>mdi-close</v-icon></v-btn>
        </v-card-title>
        <v-divider />
        <v-card-text>
          <v-form v-model="valid" lazy-validation ref="form">
            <v-text-field v-model="nombre" label="Nombre del estado del trabajo" :rules="[v => !!v || 'Es necesario escribir el estado del trabajo.']" />
          </v-form>
        </v-card-text>
        <v-card-actions class="justify-end">
          <v-btn @click="crear(dialog)" color="success" outlined :disabled="!valid">Crear</v-btn>
        </v-card-actions>
      </v-card>
    </template>
  </v-dialog>
</template>

<script>
export default {
  props: {
    dialogo: { type: Boolean, default: true }
  },
  data: () => ({
    nombre: "",
    valid: true
  }),
  computed: {
    cargando() { return this.$store.getters["proyecto/getCargando"] }
  },
  methods: {
    async crear(dialog) {
      if(this.$refs.form.validate()){
        const { nombre } = this
        const { mensaje, estado, error } = await this.$store.dispatch("trabajo/crearEstadoTrabajo", nombre );
        this.$store.commit("alerta/addAlertas", { "estado": estado, "mensaje": mensaje, "error": error })
        if(!error){
          this.cerrrarDialogo(dialog)
        }
      }
    },
    cerrrarDialogo(dialog){
      dialog.value = false
      this.$emit("emitDialogo", { dialogo: false })
      this.nombre = ""
      this.$refs.form.reset()
    }
  },
};
</script>
