<template>
    <v-dialog v-model="dialog" scrollable max-width="400">
      <template v-slot:activator="{ on, attrs }">
        <v-btn v-bind="attrs" v-on="on" color="primary" class="ma-1">
          <v-icon left>mdi-filter-variant</v-icon> Filtros
        </v-btn>
      </template>
      <v-card class="mx-auto">
        <v-card-title>
          <v-btn icon v-if="paso_filtro === 1" @click="dialog=false"><v-icon>mdi-chevron-left</v-icon></v-btn>
          <v-btn icon v-else @click="paso_filtro=1, filtro = -1"><v-icon>mdi-chevron-left</v-icon></v-btn>
          <v-spacer />
            {{ filtro > -1 ? opciones_filtrado[filtro].text : "" }}
          <v-spacer />
          <v-btn icon color="primary" @click="cleanAll()"><v-icon>mdi-eraser</v-icon></v-btn>
        </v-card-title>
        <v-divider />
          <v-stepper v-model="paso_filtro" elevation="0">
            <v-stepper-items>
              <v-stepper-content step="1">
                <v-list>
                  <v-list-item-group v-model="filtro">
                    <v-list-item v-for="(filtro, i) in opciones_filtrado" :key="i"
                      @click="paso_filtro = i+2"
                    >
                      <template v-slot:default="{active}">
                        <v-list-item-content>
                          <v-list-item-title>{{ filtro.text }}</v-list-item-title>
                          <v-list-item-subtitle>{{privilegio.nombre}}</v-list-item-subtitle>
                        </v-list-item-content>
                        <v-list-item-action>
                          <v-btn :input-value="active" icon><v-icon>mdi-chevron-right</v-icon></v-btn>
                        </v-list-item-action>
                      </template>
                    </v-list-item>
                  </v-list-item-group>
                </v-list>
              </v-stepper-content>
              <v-stepper-content step="2">
                <v-select
                  v-model="privilegio"
                  :items="privilegios"
                  label="Privilegios"
                  item-text="nombre"
                  return-object
                  class="mt-1"
                  outlined
                  dense
                  clearable
                />
              </v-stepper-content>
            </v-stepper-items>
          </v-stepper>
          <v-card-actions>
            <v-btn block color="primary" @click="obtenerUsuariosPrivilegios(privilegio)">Ver Usuarios</v-btn>
          </v-card-actions>
      </v-card>
    </v-dialog>
  </template>
  
  <script>
  export default {
    data: () => ({
      dialog: false,
      privilegio_selecconado: '',
      opciones_filtrado: [ {"text": "Privilegios"}],
      paso_filtro_seleccionado: 1,
      filtro: -1
    }),
    computed: {
      privilegios() { return this.$store.getters["grupo/getGrupos"]; },
      privilegio:{
        get(){ return this.privilegio_selecconado },
        set(nuevoValor){
            if(nuevoValor === null){ this.obtenerUsuariosPrivilegios('') }
            this.privilegio_selecconado = nuevoValor ? nuevoValor : ''
        }
      },
      paso_filtro:{
        get(){ return this.paso_filtro_seleccionado },
        set(nuevoValor){
            this.paso_filtro_seleccionado = nuevoValor
            if (nuevoValor == 2 && this.privilegios.length == 0){ this.obtenerGrupos() }
        }
      }
    },
    methods: {
        async obtenerGrupos(){ await this.$store.dispatch("grupo/obtenerGrupos") },
        async obtenerUsuariosPrivilegios({id}){
            const {mensaje, estado, error} = await this.$store.dispatch("usuario/obtenerUsuarios", {privilegio: id ? id : ''})
            if(this.privilegio){
                this.$store.commit("alerta/addAlertas", { "estado": estado, "mensaje": mensaje, "error": error })
            }
        },
        cleanAll(){
            this.privilegio_selecconado = ''
        }
    }
  };
  </script>