<template>
  <v-card class="mx-auto" outlined>
    <v-card-title class="grey lighten-2">
      Datos generales de la asignación de trabajos
    </v-card-title>
    <br>
    <v-card-text>
      <v-form ref="form" v-model="valid" lazy-validation>
        <v-row justify="space-around">
          <v-col cols="12" sm="6">
            <v-select
              v-model="proyecto"
              label="Proyectos"
              :items="proyectos"
              :rules="[v => Boolean(Object.keys(v || {})[0]) || 'Se debe elejir un proyecto']"
              item-text="nombre"
              :loading="isCargando"
              no-data-text="Proyectos no disponibles"
              clearable
              return-object
              outlined
            >
            <template v-slot:append-outer>
              <v-icon @click="isDialogCrearNuevoProyecto=true" color="info">mdi-plus</v-icon>
            </template>
          </v-select>
          </v-col>
          <v-col cols="12" sm="6">
            <v-select
              v-model="conceptosSeleccionados"
              label="Conceptos"
              :items="conceptos"
              :rules="[(v) => !!v.length > 0 || 'Se debe elejir por lo menos un concepto']"
              item-text="nombre"
              item-value="id"
              no-data-text="Conceptos no disponibles"
              :loading="isCargandoConcepto"
              small-chips
              multiple
              outlined
              clearable
              :disabled="proyecto.id?false:true"
            >
            <template v-slot:append-outer>
              <v-icon :disabled="!'id' in proyecto" @click="isDialogCrearNuevoConcepto=true" color="info">mdi-plus</v-icon>
            </template>
            </v-select>
            </v-col>
            <v-col cols="12" sm="6">
              <v-select
                v-model="estado"
                label="Estado"
                :items="status"
                item-text="nombre"
                item-value="id"
                :rules="[(v) => !!v > 0 || 'Se debe elejir el estado del trabajo']"
                no-data-text="Estados no disponibles"
                outlined
                clearable
              >
              <template v-slot:append-outer>
                <v-icon color="info" @click="isDialogCrearNuevoEstadoTrabjo=true" >mdi-plus</v-icon>
              </template>
              </v-select>
            </v-col>
            <v-col cols="12" sm="6">
              <v-select
              v-model="usuario"
              label="Usuario"
              :items="usuarios"
              item-text="nombre"
              item-value="id"
              :rules="[(v) => !!v > 0 || 'Se debe elejir un usuario']"
              no-data-text="Usuarios no disponibles"
              outlined
              clearable
              />
            </v-col>
          </v-row>
        </v-form>
      </v-card-text>
      <v-card-actions>
        <v-spacer />
        <v-btn
        @click="agregar()"
        color="green"
        :disabled="!valid"
        :loading="isCargandoBtn"
        block
        >Asignar</v-btn>
      </v-card-actions>
      <formulario-crear-nuevo-proyecto :dialog="isDialogCrearNuevoProyecto" @emitDialog="setDialogoFormularioCrearNuevoProyecto"/>
      <formulario-crear-nuevo-concepto :dialogo="isDialogCrearNuevoConcepto" @emitDialogo="setDialogoFormularioCrearNuevoConcepto"/>
      <formulario-crear-nuevo-estado-trabajo :dialogo="isDialogCrearNuevoEstadoTrabjo" @emitDialogo="setDialogoFormularioCrearEstadoTrabajo"/>
    </v-card>
  </template>

<script>
import FormularioCrearConcepto from "../../components/Proyecto/CrearConcepto.vue";
import FormularioCrearProyecto from "../../components/Proyecto/CrearProyecto.vue";
import FormularioCrearEstadoTrabajo from "../../components/trabajo/CrearNuevoEstado.vue"

export default {
  data: () => ({
    estado: 0,
    usuario: 0,
    valid: true,
    conceptosSeleccionados: [],
    isCargandoBtn: false,
    isDialogCrearNuevoProyecto: false,
    isDialogCrearNuevoConcepto: false,
    isDialogCrearNuevoEstadoTrabjo: false
  }),
  components: {
    "formulario-crear-nuevo-concepto": FormularioCrearConcepto,
    "formulario-crear-nuevo-proyecto": FormularioCrearProyecto,
    "formulario-crear-nuevo-estado-trabajo": FormularioCrearEstadoTrabajo
  },
  created() {
    this.inicializarProyectos();
    this.inicializarUsuario();
    this.inicializarStatus();
  },
  computed: {
    proyectos() { return this.$store.getters['proyecto/getProyectos'] },
    proyecto: {
      get(){ return this.$store.getters['proyecto/getProyecto'] },
      async set(nuevoValor){
        if(nuevoValor){
          this.$store.dispatch("proyecto/obtenerProyecto", nuevoValor)
          this.obtenerConceptos(nuevoValor)
        } else {
          this.$store.commit("proyecto/clearProyecto")
          this.conceptosSeleccionados = []
        }
      }
    },
    conceptos() { return this.$store.getters['proyecto/getConceptos'] },
    usuarios() { return this.$store.getters["usuario/getUsuarios"] },
    status() { return this.$store.getters["trabajo/getStatusTrabajo"] },
    inmuebles(){ return this.$store.getters['inmueble/getInmueblesSeleccionados'] },
    isCargando() { return this.$store.getters['proyecto/getCargando'] },
    isCargandoConcepto() { return this.$store.getters['proyecto/getCargandoConceptos'] },
    id_usuario() { return this.$store.getters["MOD_autenticacion/obtenerIdUsuario"] },
    asignacionesVerificadas() { return this.$store.getters["trabajo/getAsignacionesVerificadas"] },
  },
  methods: {
    async agregar() {
      if (this.$refs.form.validate() && !this.isCargandoBtn) {
        this.isCargandoBtn = true
        const { inmuebles, id_usuario, conceptosSeleccionados, usuario, estado } = this
        var verificarAsignaciones = []
        const trabajos = []

        for(const concepto of conceptosSeleccionados){
          trabajos.push({'concepto': concepto, 'usuario_asignado': usuario, 'estado': estado})
        }
        for (const inmueble of inmuebles){
          verificarAsignaciones.push({
            'inmueble': inmueble.id,
            'usuario_area': id_usuario,
            'trabajos': trabajos
          })
        }
        const { mensaje, error } = await this.$store.dispatch('trabajo/verificarAsignaciones', verificarAsignaciones )
        this.$store.commit("alerta/addAlertas", { "estado": estado, "mensaje": mensaje, "error": error })
        if (!error){
          this.$refs.form.reset()
        }
      }
      this.isCargandoBtn = false
    },
    async inicializarProyectos() { await this.$store.dispatch("proyecto/obtenerProyectos") },
    async inicializarStatus() { await this.$store.dispatch("trabajo/obtenerEstadosTrabajo") },
    async obtenerConceptos({id}) {
      const { mensaje, estado, error } = await this.$store.dispatch("proyecto/obtenerConceptos", id)
      if (error){
        this.$store.commit("alerta/addAlertas", { "estado": estado, "mensaje": mensaje, "error": error })
      }
    },
    async inicializarUsuario() {
      const { mensaje, estado, error } = await this.$store.dispatch("usuario/obtenerUsuarios", {privilegio: ''});
      if(error){
        this.$store.commit("alerta/addAlertas", { "estado": estado, "mensaje": mensaje, "error": error })
      }
    },
    setDialogoFormularioCrearNuevoProyecto({dialog}){ this.isDialogCrearNuevoProyecto = dialog },
    setDialogoFormularioCrearNuevoConcepto({dialogo}){ this.isDialogCrearNuevoConcepto = dialogo },
    setDialogoFormularioCrearEstadoTrabajo({dialogo}){ this.isDialogCrearNuevoEstadoTrabjo = dialogo }
  },
};
</script>
