<template>
  <v-card-actions>
    <v-btn
      v-for="(link, index) in links" :key="index"
      @click="redireccionar(link.nombre)"
      text
    >
      {{ link.texto }}
    </v-btn>
  </v-card-actions>
</template>

<script>
export default {
  data: () => ({
    links: [
      { texto: "Trabajos", nombre: "TrabajoUsuario" },
      { texto: "Recursos", nombre: "recursos-usuario" },
      { texto: "Pagos", nombre: "pagos" },
    ],
  }),
  computed: {
    usernname() {
      return this.$store.getters["MOD_autenticacion/getNombreUsuario"];
    },
  },
  methods: {
    redireccionar(nombre) {
      this.$router
        .push({
          name: nombre,
          params: { nombreUsuario: this.usernname },
        })
        .catch(() => {});
    },
  },
};
</script>
