import MOD_autenticacion from "./modulos/autenticacion"
import usuario from "./modulos/usuarios"
import grupo from "./modulos/grupos"
import cliente from "./modulos/clientes"
import inmueble from "./modulos/inmuebles"
import ubicacion from "./modulos/ubicaciones"
import trabajo from "./modulos/trabajos"
import proyecto from "./modulos/proyecto"
import MOD_notificaciones from "./modulos/notificaciones"
import recurso from "./modulos/recurso"
import beneficiario from "./modulos/beneficiario"
import alerta from "./modulos/alerta"
import conexion from "./modulos/conexion"

import { INICIALIZAR_FUNCIONES, LOGOUT_FUNCTION } from "./constants"

const modules = {
  MOD_autenticacion,
  usuario,
  grupo,
  cliente,
  inmueble,
  ubicacion,
  trabajo,
  MOD_notificaciones,
  recurso,
  proyecto,
  beneficiario,
  alerta,
  conexion
}

function getModulesWithAction(allModules, actionName) {
  return Object
    .keys(allModules)
    .filter((moduleKey) => {
      const { actions, namespaced } = allModules[moduleKey];
      if (namespaced !== true) return false;

      return Object
        .prototype
        .hasOwnProperty
        .call(actions, actionName);
    });
}

const modulesToInit = getModulesWithAction(modules, INICIALIZAR_FUNCIONES);
const modulesWithLogoutAction = getModulesWithAction(modules, LOGOUT_FUNCTION);


export {
  modules,
  modulesToInit,
  modulesWithLogoutAction,
};