<template>
  <v-menu bottom rounded min-width="50px" left offset-y>
    <template v-slot:activator="{ attrs, on }">
      <v-badge overlap bordered :content="numeroNotificaciones" color="green">
        <v-avatar v-bind="attrs" v-on="on" size="30">
          <v-avatar>
            <v-icon>
              mdi-bell
            </v-icon>
          </v-avatar>
        </v-avatar>
      </v-badge>
    </template>
    <v-card>
        <v-system-bar>
          <span>Tus notificaciones</span>
          <v-spacer />
          <v-btn icon>

            <v-icon @click="traerNotificaciones">mdi-reload</v-icon>
          </v-btn>
        </v-system-bar>
      <v-divider></v-divider>
        <v-list two-line>
          <v-list-item-group
            v-model="selected"
            active-class="blue--text"
          >
            <template v-for="(item, index) in items">
              <v-list-item :key="item.title">
                <template v-slot:default="{  }">
                  <v-list-item-content>
                    <v-list-item-title v-text="item.title"></v-list-item-title>
    
                    <v-list-item-subtitle
                      class="text--primary"
                      v-text="item.headline"
                    ></v-list-item-subtitle>
    
                    <v-list-item-subtitle v-text="item.subtitle"></v-list-item-subtitle>
                  </v-list-item-content>
    
                  <v-list-item-action>
                    <v-list-item-action-text v-text="item.action"></v-list-item-action-text>
                  </v-list-item-action>
                </template>
              </v-list-item>
    
              <v-divider
                v-if="index < items.length - 1"
                :key="index"
              ></v-divider>
            </template>
          </v-list-item-group>
        </v-list>
      <v-divider></v-divider>
      <v-system-bar>
        <v-spacer />
        <v-btn  small text>Ver todas</v-btn>
      </v-system-bar>
      
    </v-card>
    <!-- <v-list nav dense>
      <v-card v-if="numeroNotificaciones" class="mx-auto" max-width="400">
        <v-list-item-group
          v-model="selected"
          active-class="blue--text"
          multiple
        >
          <template v-for="(notificacion, index) in notificaciones">
            <v-list-item :key="notificacion.id">
              <template v-slot:default="{ }">
                <v-list-item-content @click="verTrabajo(notificacion)">
                  <v-list-item-title
                    v-if="isComentario(notificacion.notify_type)"
                  >
                    Han comentado un trabajo que tienes asignado.
                    <br />
                    <v-icon color=" darken-4">
                      mdi-open-in-new
                    </v-icon>
                  </v-list-item-title>
                </v-list-item-content>
              </template>
            </v-list-item>
            <v-divider
              v-if="index < notificaciones.length - 1"
              :key="index"
            ></v-divider>
          </template>
        </v-list-item-group>
      </v-card>
      <v-card v-else >
        <v-card-text>No tienes notificaciones.</v-card-text>
      </v-card>
    </v-list> -->
  </v-menu>
</template>

<script>
export default {
  data: () => ({
    selected: [],
    items: [
        {
          action: '1 dia',
          headline: 'trabajo id 1',
          subtitle: `Este tabajo fue modificado`,
          title: 'Trabajos notificación prueba #8',
        },
        {
          action: '2 dia',
          headline: 'Recurso 3 comentario 2',
          subtitle: `El usuario 2 hizo un comentario`,
          title: 'Comentarios recursos notificación prueba #4',
        },
        {
          action: '6 dia',
          headline: 'Recurso 3 archivo 2',
          subtitle: 'El usuario 1 subió un archivo',
          title: 'Archivo recursos notificación prueba #3',
        },
      ],
  }),
  computed: {
    notificaciones() {
      return this.$store.getters["MOD_notificaciones/getNotificaciones"];
    },
    id_usuario() {
      return this.$store.getters["MOD_autenticacion/obtenerIdUsuario"];
    },
    numeroNotificaciones() {
      var contar = 0;
      const { notificaciones, selected } = this;
      for (let index = 0; index < notificaciones.length; index++) {
        if (!notificaciones[index].is_read) {
          contar = contar + 1;
          selected.push(index);
        }
      }
      return contar;
    },
  },
  created() {
    // this.traerNotificaciones();
  },
  methods: {
    async traerNotificaciones() {
      const { id_usuario } = this;
      const { error } = await this.$store.dispatch(
        "MOD_notificaciones/traerNotificaciones",
        {
          id_usuario: id_usuario,
        }
      );
      if (error) {
        alert(error);
      }
    },
    isComentario(valor) {
      if (valor == "comentario") return true;
      return false;
    },
    async verTrabajo({ id, user, is_read, extra_id}) {
      this.$store.commit("trabajo/setTrabajo", { 
        id: extra_id, name: ''
      });
      console.log(!is_read)
      if (!is_read){
        this.$store.dispatch("MOD_notificaciones/actualizarNotificaciones", { 
          'id': id,
          'user': user,
          'is_read': true
        });
      }
      this.$router.push({
        name: "ArchivosTrabajo",
        params: {
          nombreUsuario: this.$store.getters[
            "MOD_autenticacion/getNombreUsuario"
          ],
        },
      });
    },
  },
};
</script>
