<template>
  <v-row justify="center">
    <v-col cols="8">
      <v-card class="mx-auto">
        <v-card-text>
          <listar-trabajos :objAsignar="{isAsignarBtn: true, deshabilitar: !isInmuebles, redireccionar: 'asignar-trabajos'}" :headers="headers"/>
        </v-card-text>
      </v-card>
    </v-col>
  </v-row>
</template>

<script>
import ListarTrabajos from "../../components/trabajo/TablaTrabajos.vue";
export default {
  data: () => ({
    headers: [
      { value: "proyecto", text: "Proyecto" },
      { value: "concepto", text: "Concepto" },
      { value: "status", text: "Status" },
      { value: "inmueble.cliente", text: "Cliente" },
      { value: "inmueble.nombre", text: "Inmueble" },
      { value: "inmueble.estado", text: "Estado inmueble" },
      { value: "usuario_asignado", text: "Asignado" },
      { value: "usuario_area", text: "Responsable" },
      { value: "creado", text: "Creado" },
      { value: "modificado", text: "Modificado" },
    ]
  }),
  components: {
    "listar-trabajos": ListarTrabajos,
  },
  computed:{
    cliente() { return this.$store.getters["cliente/getCliente"] },
    isInmuebles() { return this.$store.getters["inmueble/getInmuebles"].length; },
  },
  created(){
    this.inicializarTrabajosCliente()
  },
  methods:{
    async inicializarTrabajosCliente(){
      await this.$store.dispatch("trabajo/obtenerTrabajosCliente", {cliente: this.cliente.id})
    }
  }
};
</script>
