import axios from "@/api"

// import { localStorage2json, json2localStorage } from "@/utils/data/store";

// import { INICIALIZAR_FUNCIONES, LOGOUT_FUNCTION } from "@/store/constants"

export default {
  namespaced: true,

  state: {
    respuesta: null,

    inmueble: {},
    inmuebles: [],
    inmueblesSeleccionados: [],
    inmueblesVerificados: [],
    verificarInmuebles: [],
    numeroInmueblesCreados: 0,
    total_inmuebles_verificados: 0,

    cargando: false,
  },

  mutations: {
    setInmueble(state, payload) { state.inmueble = payload },
    setInmuebles(state, payload) {
      // json2localStorage("inmuebles", payload)
      state.inmuebles = payload
    },
    addInmuebles(state, payload){
      state.inmuebles.push(payload)
    },
    cleanInmuebles(state) {
      // json2localStorage("inmuebles", [])
      state.inmuebles = []
    },
    clearInmueble(state) {
      // json2localStorage("inmuebles", [])
      state.inmueble = {}
    },
    setCargando(state, payload) { state.cargando = payload },
    setInmueblesSeleccionados(state, payload) { state.inmueblesSeleccionados = payload },
    clearInmueblesSeleccionados(state) { state. inmueblesSeleccionados = []},
    eliminarInmueble(state, payload) { state.inmuebles.splice(payload, 1) },
    splitInmueblesVerificados(state, payload) { state.inmueblesVerificados.splice(payload, 1) },
    splitVerificarInmuebles(state, payload) { state.verificarInmuebles.splice(payload, 1) },
    setInmueblesVerificados(state, payload) { state.inmueblesVerificados = payload },
    setVerificarInmuebles(state, payload) { state.verificarInmuebles = payload },
    clearInmueblesVerificados(state) { state.inmueblesVerificados = [] },
    clearVerificarInmuebles(state) { state.verificarInmuebles = [] },
    updateInmueblVerificado(state, {indice, inmueble}) {
      if("property_key" in inmueble){ state.inmueblesVerificados[indice].clave = inmueble.property_key[0] + "(" + state.verificarInmuebles[indice].clave +  ")" }
      if("name" in inmueble){ state.inmueblesVerificados[indice].nombre = inmueble.name[0] +"(" + state.verificarInmuebles[indice].nombre +  ")"}
      if("province" in inmueble){ state.inmueblesVerificados[indice].estado = inmueble.province[0] + "(" + state.verificarInmuebles[indice].estado +  ")"}
      if("municipality" in inmueble){state.inmueblesVerificados[indice].municipio = inmueble.municipality[0] + "(" + state.verificarInmuebles[indice].municipio +  ")"}
    },
    setNumeroInmueblesCreados(state, payload){ state.numeroInmueblesCreados = payload },
    setTotalInmueblesVerificados(state, payload){ state.total_inmuebles_verificados = payload },
    setStatusInmuebleVerificado(state, {indice, status}){
      state.inmueblesVerificados[indice].status = status
    }
  },

  actions: {
    // [INICIALIZAR_FUNCIONES]({ dispatch }) {
    //   dispatch("iniciarInmuebles");
    // },
    // [LOGOUT_FUNCTION]({ commit }) {
    //   commit("limpiarInmuebles")
    // },
    // iniciarInmuebles({ commit },) {
    //   const inmuebles = localStorage2json("inmuebles")
    //   commit("MUT_ListaInmuebles", inmuebles)
    // },
    async crearInmueble({commit}, { nombre, clave, clave_2, direccion, estado, municipio, localidad, cliente }){
      try {
        commit("setCargando", true)
        const { data, status } = await axios.instance.post("inmuebles/inmueble/", {
          "name": nombre,
          "property_key": clave,
          "sirh": clave_2,
          "address": direccion,
          "province": estado.id,
          "municipality": municipio.id,
          "locality": localidad,
          "client": cliente
        })
        const { items, message } = data
        const { id, name, property_key, address, province, municipality, locality, client } = items
        const inmueble = {
          "id": id, "nombre": name, "clave": property_key, "direccion": address, "estado": province, "municipio": municipality, "localidad": locality, "cliente": client
        }
        commit("addInmuebles", inmueble)
        commit("setCargando", false)
        return { "mensaje": message, "estado": status }
      } catch (error) {
        commit("setCargando", false)
        return { "error": error }
      }
    },
    async verificarInmuebles({ commit, dispatch }, inmuebles) {
      try {
        commit("setCargando", true)
        var inmuebles_verificar = []
        inmuebles.forEach(element => {
          inmuebles_verificar.push({
            "name": element.nombre,
            "property_key": element.clave,
            "sirh": element.clave_2,
            "address": element.direccion,
            "province": element.estado,
            "municipality": element.municipio,
            "locality": element.localidad,
            "client": element.cliente.id,
          })
        })
        const { data, status } = await axios.instance.post("inmuebles/inmueble/verificacion/", {"properties":inmuebles_verificar})
        const { items, message } = data
        let list_inmuebles = []
        for(let inmueble of items){
          list_inmuebles.push({
            "status": await dispatch("isValidoInmueble", inmueble),
            "nombre": inmueble.name,
            "clave": inmueble.property_key,
            "clave_2": inmueble.sirh,
            "cliente": inmueble.client ? { "id": inmueble.client.id, "nombre": inmueble.client.name } : null,
            "estado": inmueble.province ? { "id": inmueble.province.id, "nombre": inmueble.province.name }: null,
            "municipio": inmueble.municipality ? { "id": inmueble.municipality.id, "nombre": inmueble.municipality.name } : null,
            "localidad": inmueble.locality ? { "id": inmueble.locality.id, "nombre": inmueble.locality.name } : null,
            "direccion": inmueble.address
          })
        }
        commit("setInmueblesVerificados", list_inmuebles)
        commit("setCargando", false)
        return { "mensaje": message, "estado": status }
      } catch (error) {
        commit("setCargando", false)
        return { "error": error }
      }
    },
    async crearMultiplesInmuebles({commit, state}, inmuebles) {
      try {
        commit("setCargando", true)
        var inmuebless = []
        inmuebles.forEach(inmueble => {
          inmuebless.push({
            "name": inmueble.nombre,
            "property_key": inmueble.clave,
            "sirh": inmueble.clave_2,
            "address": inmueble.direccion,
            "province": inmueble.estado ? inmueble.estado.id : null,
            "municipality": inmueble.municipio ? inmueble.municipio.id : null,
            "locality": inmueble.localidad ? inmueble.localidad.id : null,
            "client": inmueble.cliente ? inmueble.cliente.id : null,
          })
        })
        commit("setTotalInmueblesVerificados", inmuebles.length)
        const { data, status } = await axios.instance.post("inmuebles/inmueble/confirmacion/",
        { "properties": inmuebless }
        )
        var indice = 0
        const { items, message } = data
        for(const inmueble of items){
          if ("error" in inmueble){
            commit("updateInmueblVerificado", {indice: indice, inmueble: inmueble.error})
            commit("setStatusInmuebleVerificado", {inmueble: state.inmueblesVerificados[indice], indice: indice})
            indice = indice + 1
          } else {
            commit("splitInmueblesVerificados", indice)
            commit("splitVerificarInmuebles", indice)
          }
        }
        commit("setNumeroInmueblesCreados", items.length - indice)
        commit("setCargando", false)
        return { "mensaje": message, "estado": status }
      } catch (error) {
        commit("setCargando", false)
        return { "error": error }
      }
    },
    async recuperarInmueble({commit}, id_inmueble){
      try {
        commit("setCargando", true)
        const { data, status } = await axios.instance.get(`inmuebles/inmueble/${id_inmueble}`)
        const { items, message } = data
        const { id, name, property_key, address, province, municipality, locality, client } = items
        const inmueble = {
          "id": id,
          "clave": property_key,
          "nombre": name,
          "estado": province,
          "municipio": municipality,
          "localidad": locality,
          "direccion": address,
          "cliente": client
        }
        commit("setInmueble", inmueble)
        commit("setCargando", false)
        return { "mensaje": message, "status": status}
      } catch (error) {
        commit("setCargando", false)
        return { "mensaje": error.response.data.error }
      }
    },
    async listarPorClientesTerritorio({ commit }, { cliente, estado, municipio, localidad }) {
      try {
        commit("cleanInmuebles")
        commit("setCargando", true)
        const { data, status } = await axios.instance.get(
          `inmuebles/inmueble/filtrado/?clientes=${cliente}&estado=${estado ? estado.id : ''}&municipio=${municipio ? municipio.id : ''}&localidad=${localidad ? localidad.id : ''}`
        )
        const { items } = data
        var inmuebles = []
        items.forEach(inmueble => {
          inmuebles.push({
            "id": inmueble.id,
            "clave": inmueble.property_key,
            "nombre": inmueble.name,
            "estado": inmueble.province,
            "municipio": inmueble.municipality,
            "localidad": inmueble.locality,
            "direccion": inmueble.address,
            "cliente": inmueble.client
          })
        })
        commit("setInmuebles", inmuebles)
        commit("setCargando", false)
        return { "mensaje": "Consulta satisfactoria.", "estadoHTTP": status }
      } catch (error) {
        commit("setCargando", false)
        return { "error": error }
      }
    },
    async eliminar({ commit }, id) {
      commit("MUT_Flag_EliminarInmueble", 1)
      try {
        const { data } = await axios.instance.delete(`inmuebles/inmueble/${id}/`,)
        commit("MUT_Respuesta", data)
        commit("MUT_Flag_EliminarInmueble", 2)
      } catch (error) {
        commit("MUT_Flag_EliminarInmueble", 3)
      }
    },
    async listar({commit}, cliente){
      try {
        commit("setCargando", true)
        commit("cleanInmuebles")
        const { data, status } = await axios.instance.get(cliente ? `/inmuebles/inmueble/?clientes=${cliente.id}` : "/inmuebles/inmueble/")
        const { items, message } = data
        const inmuebles = []
        items.forEach(inmueble => {
          inmuebles.push({
            "id": inmueble.id,
            "clave": inmueble.property_key,
            "nombre": inmueble.name,
            "estado": inmueble.province,
            "municipio": inmueble.municipality,
            "localidad": inmueble.locality,
            "direccion": inmueble.address,
            "cliente": inmueble.client
          })
        })
        commit("setInmuebles", inmuebles)
        commit("setCargando", false)
        return { "mensaje": message, "estado": status }
      } catch (error) {
        commit("setCargando", false)
        return { "error": error }
      }
    },
    async setStatusInmuebleVerificado({commit, dispatch}, {inmueble, indice}){
      const inmueble_valido = {
        name: inmueble.nombre,
        province: inmueble.estado,
        municipality: inmueble.municipio,
        client: inmueble.cliente,
        property_key: inmueble.clave
      }
      const status = await dispatch("isValidoInmueble", inmueble_valido)
      commit("setStatusInmuebleVerificado", {indice: indice, status: status })
    },
    isValidoInmueble(context, {name, province, municipality, client, property_key}){
      let requerido = /Este campo es requerido\.\([^)]*\)/i
      let clave_existe = /Ya existe Inmueble con este Clave\.\([^)]*\)/i
      let clave_caracteres = /Asegúrese de que este campo no tenga más de 10 caracteres\.\([^)]*\)/i
      let nulo = /Este campo no puede ser nulo\.\([^)]*\)/i
      if(requerido.test(name) || name === null) { return false }
      if(property_key === null || requerido.test(property_key) || clave_existe.test(property_key) || clave_caracteres.test(property_key) || nulo.test(property_key)) { return false }
      if(province === null  || requerido.test(province) || nulo.test(province)) { return false }
      if(municipality === null || requerido.test(municipality) || nulo.test(municipality)){return false}
      if(client === null|| requerido.test(client) || nulo.test(client)){ return false }
      return true
    },
  },
  getters: {
    getInmuebles: (state) => state.inmuebles,
    getInmueble: (state) => state.inmueble,
    getCargando: (state) => state.cargando,
    getInmueblesSeleccionados: (state) => state.inmueblesSeleccionados,
    getInmueblesVerificados: (state) => state.inmueblesVerificados,
    getVerificarInmuebles: (state) => state.verificarInmuebles,
    getNumeroInmueblesCreados: (state) => state.numeroInmueblesCreados,
    getTotalInmueblesVerificados:(state) => state.total_inmuebles_verificados
  }
}