<template>
  <v-dialog
    v-model="dialogRecurso"
    transition="dialog-bottom-transition"
  >
    <v-toolbar dark color="primary" flat>
      <v-spacer />
      <v-menu offset-y left>
        <template v-slot:activator="{on, attrs}">
          <v-btn x-large icon v-on="on" v-bind="attrs"> <v-icon>mdi-dots-horizontal</v-icon></v-btn>
        </template>
        <v-list>
          <v-list-item>
            <v-list-item-action>
              <v-list-item-title>Modificar</v-list-item-title>
            </v-list-item-action>
          </v-list-item>
          <v-list-item >
            <confirmar
            mensaje="Estás a punto de eliminar esta solicitud de recursos. ¿Estás seguro?"
            :id="recurso.id"
            :indice="indice_recuros"
            :tipo_pago="('¿Eliminar ' + recurso.tipo_pago +'?')"
            @emitConfirmacion="eliminarSolicitudRecurso"
            icon="mdi-delete"
            color="error"
            />
          </v-list-item>
        </v-list>
      </v-menu>
      <v-btn x-large icon @click="dialogRecurso = false" >
        <v-icon>mdi-close</v-icon>
      </v-btn>
    </v-toolbar>
    <v-row no-gutters>
      <v-col cols="12" sm="8" class="flex-grow-1 flex-shrink-0">
        <tabla-peticiones :cabecera="headers" :recurso="recurso" />
        <archivo :recurso_id="recurso.id" />
      </v-col>
      <v-col cols="12" sm="4" class="flex-grow-1 flex-shrink-0">
        <Comentarios />
      </v-col>
    </v-row>
  </v-dialog>
</template>

<script>
import Confirmar from "../../alertas/Confirmar.vue";
import Comentarios from "../../components/Recurso/Comentarios.vue";
import TablaPeticiones from "../../components/Recurso/TablaPeticiones.vue"
import Archivo from "../../components/Recurso/Archivos.vue"

export default {
  props: {
    dialog: { type:Boolean, default:false},
    indice_recuros: { type: Number, default: -1 }
  },
  data: () => ({
    headers: [
      { text: "Solicitado", value: "trabajo.solicitado" },
      { text: "Fecha solicitado", value:"creado" },
      { text: "Gestor", value: "trabajo.gestor" },
      { text: "Cliente", value: "inmueble.cliente" },
      { text: "Clabe", value: "inmueble.clave" },
      { text: "Sucursal", value: "inmueble.nombre", sortable: false },
      { text: "Estado", value: "inmueble.estado", sortable: false },
      { text: "Municipio", value: "inmueble.municipio", sortable: false },
      { text: "Concepto", value: "trabajo.concepto", sortable: false },
      { text: "Importe", value: "importe", sortable: false },
    ],
  }),
  components: {
    Comentarios,
    Confirmar,
    "tabla-peticiones": TablaPeticiones,
    "archivo": Archivo
  },
  computed: {
    recurso() { return this.$store.getters["recurso/getRecurso"]; },
    dialogRecurso:{
      get() { return this.dialog },
      set(nuevoValor){
        this.$emit('emitDialog', nuevoValor)
        this.$store.commit("recurso/cleanRecurso")
        this.$store.commit("recurso/cleanComentarios")
        this.$store.commit("recurso/clearArchivos")
      }
    },
    cargando() { return this.$store.getters["recurso/getCargando"]; }
  },
  methods: {
    async eliminarSolicitudRecurso({confirmacion, id, indice}){
      if(confirmacion){
        const { mensaje, estado, error } = await this.$store.dispatch("recurso/eliminarSolicitudRecurso", { 'id': id, 'indice': indice })
        this.$store.commit("alerta/addAlertas", { 'estado': estado, 'mensaje': mensaje, 'error': error, });
        this.dialogRecurso = false
      }
    },
  },
};
</script>