<template>
  <v-card class="mx-auto" outlined>
    <v-card-title class="grey white--text">
      Fondos necesarios: <v-spacer /> MX$ {{total}}
    </v-card-title>
    <v-card-title class="warning lighten-4">
      <v-switch
        v-model="isPagar_separado_solicitud_recuros"
        :label="`¿Efectuar el pago por separado? ${isPagar_separado_solicitud_recuros ? 'SI' : 'NO'}`"
        inset
      />
    </v-card-title>
    <v-card-text>
      <v-form ref="formulario" v-model="valid" lazy-validation>
        <v-responsive class="overflow-y-auto" max-height="500">
          <v-data-table
            :items="peticiones_verificadas"
            :headers="cabecera"
            :footer-props="{
              itemsPerPageText: 'Filas por página',
              itemsPerPageAllText: 'Todos',
              pageText: ' p - Tp de n',
            }"
          >
            <template v-slot:item.concepto="{index}">
              <b>{{ trabajos_seleccionados[index].concepto }}</b> ({{ trabajos_seleccionados[index].proyecto }})
            </template>
            <template v-slot:item.inmueble="{index}">
              {{ trabajos_seleccionados[index].inmueble.nombre }} ({{ trabajos_seleccionados[index].inmueble.clave }})
            </template>
            <template v-slot:item.monto="{item}">
              <v-card v-if="!item.error" class="ma-1" width="200">
                <v-text-field
                  v-model="item.monto"
                  label="Importe"
                  solo
                  dense
                  autofocus
                  prefix="$"
                  type="number"
                  placeholder="1.00"
                  step="0.1"
                  min="1"
                  :rules="[(v) => v>0 || 'Es necesario escribir el monto mayor a 0.00' ]"
                />
              </v-card>
              <v-card v-else class="text-subtitle-2 text-center ma-1" color="error lighten-3" width="200">
                {{item.error}}
              </v-card>
            </template>
            <template v-slot:item.metodo_pago="{item, index}">
              <v-card v-if="!item.error" icon width="200">
                <v-text-field v-model="item.datos_bancarios" solo dense autofocus disabled :rules="[(v) => !!v || 'Seleccione un método de pago']" type="number">
                  <template v-slot:append-outer>
                    <v-icon color="success" @click= "asignarMetodoPago({'peticion': item, 'indice': index})">mdi-credit-card-plus</v-icon>
                  </template>
                </v-text-field>
              </v-card>
            </template>
            <template v-slot:item.banco="{item}">
              <v-card v-if="!item.error" class="ma-1" width="200">
                <v-text-field
                  v-model="item.banco"
                  label="Banco"
                  solo
                  dense
                  autofocus
                  :rules="[(v) => !!v || 'Es necesario escribir el banco']"
                />
              </v-card>
            </template>
            <template v-slot:item.eliminar="{index}">
              <v-btn icon @click="confirmarEliminacion(index)"><v-icon color="error">mdi-trash-can</v-icon></v-btn>
            </template>
          </v-data-table>
        </v-responsive>
      </v-form>
      <v-dialog v-model="dialog_metodo_pago" max-width="700" persistent>
        <seleccionar-metodo-pago
          :peticion="peticion"
          :editar_indice_peticion="indice_peticion"
          @emitDialogMetodoPago="setDialogMetodoPago"
        />
      </v-dialog>
    </v-card-text>
  </v-card>
</template>

<script>
import SeleccionarMetodoPago from '../../components/Seleccion/MetodosPago.vue'
export default {
  data: () => ({
    continuar_p3: true,
    checkbok: false,
    dialog_metodo_pago: false,
    peticion: null,
    indice_peticion: -1,
    cabecera: [
      {'text': 'Concepto', 'value': 'concepto'},
      {'text': 'Inmueble', 'value': 'inmueble'},
      {'text': 'Importe', 'value': 'monto'},
      {'text': 'Metodo pago', 'value': 'metodo_pago', sortable: false},
      {'text': 'Banco', 'value': 'banco'},
      {'text': 'Eliminar', 'value': 'eliminar'}
    ]
  }),
  components:{
    "seleccionar-metodo-pago": SeleccionarMetodoPago
  },
  computed: {
    trabajos_seleccionados() { return this.$store.getters["trabajo/getTrabajosSeleccionados"]; },
    peticiones_verificadas() { return this.$store.getters["recurso/getPeticionesVerificadas"] },
    total() {
      var total = 0.0
      for(const peticion of this.peticiones_verificadas){
        total = peticion.monto ? Number(peticion.monto) + total : 0.0 + total
      }
      return new Number(total.toFixed(2)).toLocaleString("es-MX", { style: "currency", currency: "MXN" })
    },
    valid: {
      get(){ return this.continuar_p3 },
      set(nuevoValor){
        this.$emit("eventoContinuar", { 'isValid_p2': nuevoValor, 'validacion': this.$refs.formulario})
      }
    },
    isPagar_separado_solicitud_recuros:{
      get() { return this.$store.getters["recurso/getSolicitudRecurso"].pago_separado },
      set(nuevoValor){
        this.$store.commit("recurso/setSolicitudRecursoPagoSeparado",nuevoValor)
      }
    }
  },
  methods: {
    setDialogMetodoPago({isDialogo}){
      this.dialog_metodo_pago = isDialogo
    },
    confirmarEliminacion(index){
      this.$store.commit("trabajo/deleteSeleccionado", index)
      this.$store.commit("recurso/deleteVerificarPeticionesVerificadas", index)
    },
    asignarMetodoPago({peticion, indice}){
      this.indice_peticion = indice
      this.peticion = peticion
      this.dialog_metodo_pago = true
    }
  },
};
</script>
