<template>
    <v-card flat>
        <v-card-title class="grey lighten-2">
            Usuarios
            <v-spacer></v-spacer>
            <v-text-field
            v-model="buscar"
                append-icon="mdi-magnify"
                label="Buscar usuaros"
                single-line
                hide-details
                clearable
                dense
                filled
                outlined
                rounded
                class="white"
            />
        </v-card-title>
        <v-card-text>
            <v-data-table
            :items="usuarios"
            :headers="cabeceras"
            :no-data-text="mensaje"
            :footer-props="{
                itemsPerPageText: 'Filas por página',
                itemsPerPageAllText: 'Todos',
                pageText: 'p - Tp de Fp',
            }"
            :search="buscar"
            >
                <template v-slot:top>
                    <v-toolbar flat>
                        <v-spacer></v-spacer>
                        <formulario-crear-nuevo-usuario />
                        <filtrar-usuarios />
                    </v-toolbar>
                </template>
                <template v-slot:item.nombre="{item}">
                    {{ item.nombre }} {{ item.apellidos }}
                </template>
                <template v-slot:item.is_activo="{item, index}">
                    <confirmar-permisos
                        :indice="index"
                        :tipo_pago="`Confirmar ${item.is_activo ? 'desactivación'  : 'activación '} de ${item.nombre_usuario}`"
                        :mensaje="`Estás a punto de ${item.is_activo ? 'desactivar'  : 'activar '} el siguiente usuario. ¿Estás seguro?`"
                        :icon="item.is_activo ? 'mdi-checkbox-marked-circle-outline' : 'mdi-close-circle-outline'"
                        :color="item.is_activo ? 'success' : 'error'"
                        @emitConfirmacion="cambiarPermisoActivo"
                    />
                </template>
                <template v-slot:item.is_personal="{item, index}">
                    <confirmar-permisos
                        :indice="index"
                        tipo_pago="Confirmar personal"
                        :mensaje="`Estás a punto de ${item.is_personal ? 'desactivar'  : 'confirmar '} el siguiente personal. ¿Estás seguro?`"
                        :icon="item.is_personal ? 'mdi-checkbox-marked-circle-outline' : 'mdi-close-circle-outline'"
                        :color="item.is_personal ? 'success' : 'error'"
                        @emitConfirmacion="cambiarPermisoPersonal"
                    />
                </template>
                <template v-slot:item.is_superusuario="{item}">
                    <v-icon :color="item.is_superusuario ? 'success' : 'error'" disabled>
                        {{ item.is_superusuario ? 'mdi-checkbox-marked-circle-outline' : 'mdi-close-circle-outline' }}
                    </v-icon>
                </template>
                <template v-slot:item.accion="{item}">
                    <v-menu left offset-y>
                        <template v-slot:activator="{ attrs, on }">
                            <v-btn icon v-bind="attrs" v-on="on" color="info">
                                <v-icon>mdi-account-edit</v-icon>
                            </v-btn>
                        </template>
                        <v-list>
                            <v-list-item link :disabled="!(item.is_activo && item.is_personal)">
                                <v-list-item-title>
                                    <formulario-asignar-privilegios-usuario
                                        :usuario="item"
                                    />
                                </v-list-item-title>
                            </v-list-item>
                          <v-list-item link color="erro">
                                <v-list-item-title> Eliminar </v-list-item-title>
                          </v-list-item>
                        </v-list>
                    </v-menu>
                </template>
            </v-data-table>
      </v-card-text>
    </v-card>
</template>
<script>
import FormularioAsignarPrivilegiosUsuario from "../../components/Usuario/FormularioAsignarPrivilegiosUsuario.vue"
import FormularioCrearNuevoUsuario from "../../components/Usuario/FormularioCrearNuevoUsuario.vue";
import Confirmar from "../../alertas/Confirmar.vue"
import Filtrado from "./Filtrado.vue"
export default {
  props: {
      usuarios: { type: Array, default: () => ([]) },
      cabeceras: { type: Array, default: () => ([]) }
  },
  data:()=>({
    buscar: "",
  }),
  computed:{
      mensaje() { return "No se encontraron usuarios." }
  },
  components: {
    "formulario-crear-nuevo-usuario": FormularioCrearNuevoUsuario,
    "confirmar-permisos": Confirmar,
    "formulario-asignar-privilegios-usuario": FormularioAsignarPrivilegiosUsuario,
    "filtrar-usuarios": Filtrado
  },
  methods:{
      async cambiarPermisoActivo({indice, confirmacion}){
        if(confirmacion){
            const {id, is_activo, is_personal} = this.usuarios[indice]
            const { mensaje, error, estado } = await this.$store.dispatch("usuario/actulizarPermisos", {
                id, indice, is_activo: !is_activo, is_personal
            })
            this.$store.commit("alerta/addAlertas", { estado: estado, mensaje: mensaje, error: error })
        }
      },
      async cambiarPermisoPersonal({indice, confirmacion}){
        if(confirmacion){
            const {id, is_activo, is_personal} = this.usuarios[indice]
            const { mensaje, error, estado } = await this.$store.dispatch("usuario/actulizarPermisos", {
                id, indice, is_activo, is_personal: !is_personal
            })
            this.$store.commit("alerta/addAlertas", { estado: estado, mensaje: mensaje, error: error })
        }
      }
  }
};
</script>

  