import axios from '../../api'

const state = {
    cargando: false
}

const mutations = {
    setCargando(state, payload){
        state.cargando = payload
    }
}

const actions = {
    async ping({commit}) {
        try {
            commit("setCargando", true)
            await axios.instance.get()
            return true
        } catch (error) {
            commit("setCargando", false)
            return false
        }
    }
}

const getters = {
    getCargando:(state) => state.cargando
}

export default {
    namespaced: true,
    state,
    mutations,
    actions,
    getters
}
