import Vue from "vue";
import VueRouter from "vue-router";
import Login from "../views/Login.vue";

import Home from "@/views/Home";

import Layout from "../cabeceras/MenuNavegacionInmueble.vue";
import HomeInmuebles from "../views/Inmueble/Inmueble.vue";
import CargarArchivo from "../views/Inmueble/CrearInmuebles.vue";

import Trabajos from "@/views/Trabajo/AsignarTrabajos";
import TrabajosUsuario from "@/views/Trabajo/TrabajosUsuario.vue";
import TrabajoUsuario from "@/views/Trabajo/ArchivosPorTrabajo.vue";
import TrabajosInmueble from "../views/Trabajo/TrabajosCliente.vue"

import HomeRecurso from "../views/Recurso/Home.vue"
import Recurso from "../views/Recurso/SolicitudesRecursos.vue"
import Pagos from "../views/Pago/Pagos.vue"
import CrearRecurso from "../views/Recurso/CrearRecurso.vue"
import RecursosUsuario from "../views/Recurso/SolicitudesRecursosCreadas.vue"

import LayoutUsuarios from "../cabeceras/MenuNavegacionUsuarios.vue";
import HomeUsuarios from "../views/Usuario/Home.vue";
import Configuraciones from "../views/Usuario/Configuraciones.vue"

import AdministrarPersonal from "../views/Personal/AdministrarEquipo.vue"

import ConfiguracionCliente from "../views/Configuraciones/ClienteInmuebles.vue"

import LayoutConfiguracion from "@/cabeceras/LayoutConfiguraciones";
import EditarPerfil from "@/views/Configuraciones/Perfil";

import store from "@/store/index";


import de from "../views/Trabajo/VerTrabajo.vue"

Vue.use(VueRouter);

const enterSiUsuarioAutenticado = (to, from, next) => {
  if (store.getters["MOD_autenticacion/isUsuarioAutenticado"]) {
    next();
  } else {
    next("/login")
  }
};

const routes = [
  {
    path: "/login",
    name: "Login",
    component: Login,
  },
  {
    path: "/:nombreUsuario",
    name: "HomeUsuario",
    component: HomeUsuarios,
    meta: { requiereAuth: true },
    children: [
      {
        path: "cuenta"
      },
      {
        path: "trabajos",
        name: "TrabajoUsuario",
        component: TrabajosUsuario
      },
      {
        path: "trabajo/archivos",
        name: "ArchivosTrabajo",
        component: TrabajoUsuario,
        props: true
      },
      {
        path: "trabajos/nuevos",
        name: "asignar-trabajos-usuario",
        component: Trabajos,
        props: { sinClienteSeleccionado: true }
      },
      {
        path: "pagos",
        name: "pagos",
        component: Pagos
      }
    ]
  },
  {
    path: "/:nombreUsuario/recursos",
    component: HomeRecurso,
    meta: { requiereAuth: true },
    children: [
      {
        path: "",
        name: "recursos-usuario",
        component: RecursosUsuario
      },
      {
        path: "solicitudes",
        name: "solicitudes",
        component: Recurso,
      },
    ]
  },
  {
    path: "/:nombreUsuario/recursos/nuevo",
    name: "peticion-recursos",
    component: CrearRecurso,
  },
  {
    path: "/",
    name: "Home",
    component: Home,
    beforeEnter: enterSiUsuarioAutenticado
  },
  {
    path: "/personal",
    component: LayoutUsuarios,
    meta: { requiereAuth: true },
    children: [
      {
        path: "usuarios",
        name: "home-personal",
        component: AdministrarPersonal,
      },
      {
        path: "configuraciones",
        name: "configuraciones",
        component: Configuraciones
      }
    ]
  },
  {
    path: "/de",
    name: "De",
    component: de
  },
  {
    path: "/:cliente",
    component: Layout,
    meta: { requiereAuth: true },
    children: [
      {
        path: "inmuebles",
        name: "inmuebles",
        component: HomeInmuebles,
      },
      {
        path: "inmuebles/cargar/",
        name: "cargar-archivo",
        component: CargarArchivo,
      },
      {
        path: "trabajos",
        name: "trabajos-inmuebles",
        component: TrabajosInmueble
      },
      {
        path: "trabajos/nuevos",
        name: "asignar-trabajos",
        component: Trabajos,
      },
      {
        path: "configuraciones",
        name: "configuraciones-cliente",
        component: ConfiguracionCliente
      }
    ],
  },
  {
    path: "/confuguracion",
    component: LayoutConfiguracion,
    children: [
      {
        path: "perfil",
        name: "Editar-perfil",
        component: EditarPerfil,
      }
    ]
  }
];

const router = new VueRouter({
  mode: "history",
  base: process.env.BASE_URL,
  routes,
});

router.beforeEach((to, from, next) => {
  if (to.matched.some((record) => record.meta.requiereAuth)) {
    if (!store.getters["MOD_autenticacion/isUsuarioAutenticado"]) {
      next({
        name: "Login",
      });
    } else {
      next();
    }
  } else {
    next();
  }
});
export default router;
