<template>
    <v-dialog v-model="dialogo" persistent max-width="300">
        <v-card class="mx-auto">
          <v-card-title class="text-h6 grey lighten-2">
            {{tipo_mensaje}}
          </v-card-title>
          <v-card-text>{{mensaje}}</v-card-text>
          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn color="error darken-1" text @click="cerrrarDialogo()">
              Cancelar
            </v-btn>
            <v-btn color="green darken-1" text @click="aceptarConfirmacion()">
              Aceptar
            </v-btn>
          </v-card-actions>
        </v-card>
    </v-dialog>
  </template>
  
  <script>
  export default {
    props: {
      mensaje: { type: String, default: "" },
      tipo_mensaje: { type: String, default: "" },
      dialogo: { type: Boolean, default: false}
    },
    methods: {
      aceptarConfirmacion(){
        this.$emit("emitConfirmacion", { 'confirmacion':true, dialogo: false })
      },
      cerrrarDialogo() {
        this.$emit("emitConfirmacion", { 'confirmacion':false, dialogo: false})
      },
    },
  };
  </script>