<template>
  <v-row dense justify="center">
    <v-col cols="12" sm="9" md="9" lg="9" xl="9">
      <v-stepper v-model="e1">
        <v-stepper-header>
          <v-stepper-step :complete="e1 > 1" step="1">
            Paso 1 <br />
            Inmuebles
          </v-stepper-step>

          <v-divider />

          <v-stepper-step :complete="e1 > 2" step="2">
            Paso 2 <br />
            Datos genereales
          </v-stepper-step>

          <v-divider />

          <v-stepper-step step="3">
            Paso 3 <br />
            Asignar
          </v-stepper-step>
        </v-stepper-header>
        <v-stepper-items>
          <v-stepper-content step="1">
            <inmuebles-tabla :sinClienteSeleccionado="sinClienteSeleccionado"/>
            <br />
            <v-btn
              color="success"
              block
              @click="e1 = 2"
              :disabled="isInmueblesSeleccionados"
            >
              Continue
            </v-btn>
          </v-stepper-content>

          <v-stepper-content step="2">
            <v-btn color="warning" block @click="e1 = 1"> Regresar </v-btn>
            <br />
            <formulario-crear-trabajo />
            <br />
            <v-btn
              color="primary"
              @click="e1 = 3"
              block
              :disabled="!asignaciones_v.length"
            >
              Continue
            </v-btn>
          </v-stepper-content>
          <v-stepper-content step="3">
            <v-btn color="warning" block @click="e1 = 2"> Regresar </v-btn>
            <br />
            <CompPru12 />
            <br />
            <v-btn color="primary" block @click="dialogoAlertaFormulario=true"> Finalizar </v-btn>
          </v-stepper-content>
        </v-stepper-items>
      </v-stepper>
    </v-col>
    <v-col cols="12" sm="3" md="3" lg="3" xl="3">
      <v-card class="mx-auto" outlined>
        <v-card-title>Resumen </v-card-title>
        <v-divider />
        <v-card-text>
          Inmuebles: <br>
          Num. trabajos: <br>
          Num. trabajos por confirmar:
        </v-card-text>
        <v-card-actions>
          <v-spacer />
          <v-btn rounded color="error" @click="cancelarAsignacion()">
            Cancelar
            <v-icon right>mdi-close-circle-outline</v-icon>
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-col>
    <dialogo-alerta-formulario
      :dialogo="dialogoAlertaFormulario"
      tipo_mensaje="Formulario crear múltiples trabajos"
      mensaje="Estás a punto de borrar toda la información. ¿Estás seguro?"
      @emitConfirmacion="setDialogoAlertaFormulario"
    />
  </v-row>
</template>

<script>
import InmueblesTable from "../../components/trabajo/TablaBuscarTrabajosInmueble.vue";
import FromularioCrearTrabajo from "../../components/trabajo/FormularioTrabajo.vue";
import CompPru12 from "../../components/TablaTrabajoInmuebles.vue";
import AlertaFormulario from "../../alertas/AlertaFormulario.vue";

export default {
  props: { 
    sinClienteSeleccionado: { type: Boolean, default: false }
  },
  data: () => ({
    e1: 1,
    inmuebles: [],
    isValid_p2: false,
    dialogoAlertaFormulario: false
  }),
  components: {
    "inmuebles-tabla": InmueblesTable,
    "formulario-crear-trabajo": FromularioCrearTrabajo,
    CompPru12,
    "dialogo-alerta-formulario": AlertaFormulario
  },
  computed: {
    asignaciones_v() { return this.$store.getters["trabajo/getAsignacionesVerificadas"]; },
    isInmueblesSeleccionados() {
      return this.$store.getters["inmueble/getInmueblesSeleccionados"].length ? false : true;
    },
    cliente() { return this.$store.getters["cliente/getCliente"] }
  },
  methods: {
    setValidP2({ validacion }) {
      this.isValid_p2 = validacion ? validacion.validate() : false;
    },
    setDialogoAlertaFormulario({confirmacion, dialogo}){
      if(confirmacion){
        this.cancelarAsignacion()
      }
      this.dialogoAlertaFormulario = dialogo
    },
    cancelarAsignacion(){
      this.$store.commit("inmueble/clearInmueblesSeleccionados");
      this.$store.commit("trabajo/clearAsignacionesVerificadas");
      this.$router.push({name: "trabajos-inmuebles", params: { cliente: this.cliente.nombre }})
    }
  },
};
</script>
