<template>
  <v-dialog
    v-model="objetoDialgoCambiarUsario.is_dialogo"
    @click:outside="cerrarDialogo()"
    max-width="300"
  >
    <v-card class="mx-auto">
      <v-card-title>
        <v-spacer />
        Usuarios
        <v-spacer />
        <v-icon>mdi-reload</v-icon>
      </v-card-title>
      <v-divider></v-divider>
      <v-card-text>
        <v-list>
          <v-list-item-group v-model="id_usuario_seleccionado">
            <v-list-item
              v-for="usuario in usuarios"
              :key="usuario.id"
              :value="usuario.id"
            >
              <v-list-item-avatar>
                <img :src="usuario.nombre" :alt="usuario.imagen" />
              </v-list-item-avatar>
              <v-list-item-content>
                <v-list-item-title>
                  {{ usuario.nombre }}
                </v-list-item-title>
              </v-list-item-content>
            </v-list-item>
          </v-list-item-group>
        </v-list>
      </v-card-text>
    </v-card>
  </v-dialog>
</template>

<script>
export default {
  props: {
    objetoDialgoCambiarUsario: { type: Object, default:() => ({}) }
  },
  computed: {
    usuarios() {
      return this.$store.getters["usuario/getUsuarios"];
    },
    id_usuario_seleccionado: {
      get() { return this.objetoDialgoCambiarUsario.id_usuario; },
      set(nuevoValor) {
        const { indice_inmueble, indice_trabajo } = this.objetoDialgoCambiarUsario
        const objetoUsuario = this.usuarios.find(elemento => elemento.id == nuevoValor)
        this.$emit("setDialogo",{
          is_dialogo: true,
          objetoUsuario: objetoUsuario === undefined ? {id: 0, name: ""} : { id: objetoUsuario.id, name: objetoUsuario.nombre},
          indice_inmueble: indice_inmueble,
          indice_trabajo: indice_trabajo
        });
      },
    },
  },
  methods: {
    cerrarDialogo() {
      this.$emit("setDialogo", { is_dialogo: false, objetoUsuario:{id: 0, name: ""}, indice_inmueble: -1, indice_trabajo: -1 });
    },
  },
};
</script>